@import url("../../styles//variables.scss");

.header {
    width: 100%;
    height: 58px;
    background-color: var(#{--background-header-color-default});
    margin-bottom: 55px;
    &__wrap {
        width: 100%;
        height: 58px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
        margin-left: 60px;
    }

    &__assets {
        width: auto;
        height: 23px;
        display: flex;
        align-items: center;
        justify-content: space-between !important;
        margin-right: 60px;
    }
}

@media (max-width: 1024px) {
    .header {
        position: fixed;
        z-index: 1;
        &__assets_item {
            display: none;
        }
        &__assets_item:last-child {
            display: block;
        }
        &__assets {
            width: 110px;
            margin: 0;
        }
        &__logo {
            margin: 0;
        }
    }
}
